@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&display=swap);
body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-bar{
    height: 9%;
    width: 100%;
    min-height: 25px;
    background-color: #37BBBD;
    top: 0;
    position: fixed;
    display: flex;
}

.logo-container{
    width: 20%;
    height: 100%;
    position: relative;
}

.links-container{
    width: 80%;
    position: relative;
}

.logo{
    height: 90%;
    max-width: 100%;
    left: 15%;
    position: absolute;
    object-fit: contain;
}


.nav-links {
    right: 0;
    position: fixed;
    margin: 0;
    padding: 0;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);

}

.nav-links li{
    display: inline;
}

a:link, a:visited {
    color: #FAFAFA;
    text-decoration: none;
}

.link-title {
    margin-right: 20px;
    font-weight: bold;
    font-size: 18px;
    text-decoration: none;
}

.link-title:hover {
    text-decoration: underline;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.footer {
    position: absolute;
    bottom: 0;
    background-color: #00070F;
    color: #FAFAFA;
    width: 100%;
    height: 5%;
    min-height: 25px;
}

.footer-container  {
    position: relative;
}

.footer-lists {
    position: absolute;
    margin-top: 0px;
    margin-bottom: 0px;
    padding: 0px;
    margin-left: 2%;
    margin-right: 2%;
    margin-top: 0.5%;
}

.language-list {
    left: 0px;
}

.language-list li{
    display: inline;
    padding-right: 15px;
    font-size: 12px;
}

.contact-list {
    right: 0px;
}

.contact-list li{
    display: inline;
    padding-left: 20px;
    font-size: 25px;
}

.a:link a:visited {
    margin: 0px;
}

